import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import LaunchIcon from '@mui/icons-material/Launch';
import MenuIcon from '@mui/icons-material/Menu';
import PhoneIcon from '@mui/icons-material/Phone';
import { darkTheme } from '../style';
import Logo from '../../images/logo.png';
import { scrollToTop, scrollToSection } from '../../utils/scroll';

const menuLists = [
  {
    label: 'About',
    link: '#about'
  },
  {
    label: 'Access',
    link: '#access'
  },
  { label: 'Contact', link: '#contact' },
  {
    label: 'Menu',
    link: 'https://www.instagram.com/ty.dining/',
    isExternal: true
  }
];

const MenuLink = props => (
  <Link
    color='#fff'
    underline='hover'
    {...props}
    sx={{
      margin: '0 1em',
      display: {
        sm: 'inline',
        xs: 'none'
      },
      ...props.sx
    }}
  >
    <Typography
      component='span'
      variant={props.variant || 'body2'}
      color='#fff'
    >
      {props.children}
    </Typography>
  </Link>
);

const SectionMenu = ({ tel }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = _isOpen => event => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar position='fixed' sx={darkTheme}>
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height: '68px'
          }}
        >
          <Box>
            <Link
              href='#top'
              sx={{
                display: 'block'
              }}
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
            >
              <img
                src={Logo}
                alt='TY DINING'
                width={96}
                height={36}
                style={{
                  display: 'block',
                  pointerEvents: 'none'
                }}
              />
            </Link>
          </Box>
          <Box>
            {menuLists.map(({ label, link, isExternal }, i) => {
              return (
                <MenuLink
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={e => {
                    if (!isExternal) {
                      e.preventDefault();
                      scrollToSection(link.replace(/^#(.*)$/, '$1'));
                    }
                  }}
                >
                  {label}
                  {isExternal && (
                    <LaunchIcon sx={{ ml: 0.5, width: 12, height: 12 }} />
                  )}
                </MenuLink>
              );
            })}
            <MenuLink
              sx={{ display: 'inline' }}
              href={`tel:+81-${tel}`}
              variant='body1'
              underline='always'
            >
              <PhoneIcon
                sx={{
                  mr: 0.5,
                  width: 14,
                  height: 14
                }}
              />
              0{tel}
            </MenuLink>
            <IconButton
              onClick={toggleDrawer(true)}
              sx={{
                display: {
                  sm: 'none',
                  xs: 'inline'
                }
              }}
            >
              <MenuIcon
                sx={{
                  color: '#fff'
                }}
              />
            </IconButton>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            ...darkTheme,
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {menuLists.map(({ label, link, isExternal }, i) => (
              <ListItemButton
                key={i}
                href={link}
                target={isExternal && '_blank'}
                onClick={e => {
                  if (!isExternal) {
                    e.preventDefault();
                    scrollToSection(link.replace(/^#(.*)$/, '$1'));
                  }
                }}
              >
                <ListItemText
                  primary={
                    <Typography>
                      {label}
                      {isExternal && (
                        <LaunchIcon sx={{ ml: 0.5, width: 10, height: 10 }} />
                      )}
                    </Typography>
                  }
                />
              </ListItemButton>
            ))}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};

export default SectionMenu;
