import { animateScroll, scroller } from 'react-scroll';

const smooth = 'easeOutQuint';

export const scrollToSection = (targetSectionName, offset = -68) => {
  scroller.scrollTo(targetSectionName, {
    offset,
    smooth
  });
};

export const scrollToTop = () => {
  animateScroll.scrollToTop({
    smooth
  });
};
